"use client";
import { useState } from "react";
import { useRouter } from "next/navigation";

import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { sendPasswordResetEmail } from "firebase/auth";
import { auth } from "@/app/firebase/config";
import { Toaster, toast } from "sonner";

const ForgotPassword = () => {
  const [email, setEmail] = useState("");

  const handleForgotPassword = async () => {
    if (!email) {
      toast.error("Please enter your email.");
      return;
    }

    try {
      await sendPasswordResetEmail(auth, email);
      toast.success(`Password reset email sent to ${email}`);
    } catch (error: unknown) {
      const errorMessage = error as { code: string };
      if (errorMessage.code === "auth/user-not-found") {
        toast.error("No user found with this email.");
      } else if (errorMessage.code === "auth/invalid-email") {
        toast.error("Invalid email address.");
      } else {
        toast.error("Something went wrong. Please try again.");
      }
    }
  };

  const router = useRouter();

  const goBack = () => {
    router.back();
  };

  return (
    <div className="lg:min-h-[500px] xl:min-h-[300px]">
      <Toaster richColors position="top-center" />
      <div className="text-center mt-10">
        <h1 className="text-3xl font-bold">Forgot Password</h1>
        <p className="text-base mt-1">
          A link will send to your email to reset the password
        </p>
        <div className="grid items-center justify-center mt-10">
          <div className="grid gap-2">
            <div className="grid gap-10">
              <Input
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                placeholder="Enter your email"
              />
            </div>
            <div className="flex gap-5">
              <Button onClick={handleForgotPassword}>
                Send Password Reset Email
              </Button>

              <Button onClick={goBack} variant="secondary">
                Go Back
              </Button>
            </div>
          </div>
        </div>
      </div>
      <GridPattern
        squares={[
          [4, 4],
          [5, 1],
          [8, 2],
          [5, 3],
          [5, 5],
          [10, 10],
          [12, 15],
          [17, 10],
          [10, 15],
          [12, 11],
          [7, 9],
          [18, 13],
        ]}
        className={cn(
          "[mask-image:radial-gradient(400px_circle_at_center,white,transparent)]",
          "inset-x-0 inset-y-[-20%] h-[60rem] skew-y-12"
        )}
      />
    </div>
  );
};

export default ForgotPassword;
